import React, { useEffect, useState } from 'react';
import './Test.css';
import SwipeableViews from 'react-swipeable-views';
import { TEST_DATA, TEST_RESULTS_URL } from '../test_data';

const testResult = {};

const Test = ({ history }) => {
    const [isLoading, setIsLoading] = useState(true);
    const [currentScreen, setCurrentScreen] = useState(0);

    const mouseOverEvent = {
      onMouseEnter: (e) => {
        e.target.style.background = '#FFFFFF';
        e.target.style.color = '#000000';
      },
      onPointerEnter: (e) => {
        e.target.style.background = '#FFFFFF';
        e.target.style.color = '#000000';
      },
      onMouseLeave: (e) => {
        e.target.style.background = '#000000';
        e.target.style.color = '#FFFFFF';
      },
      onPointerLeave: (e) => {
        e.target.style.background = '#000000';
        e.target.style.color = '#FFFFFF';
      }
    };

    const handleNext = (page, value) => {
        // 페이지에서 누른 버튼 값 으로 결과 저장
        testResult[page] = value;

        // 현재 페이지는 바뀌기 전이라 인덱스 0부터 포함하여 +2를 해야함
        if(currentScreen + 2 > TEST_DATA.length) {
            goToResult(testResult);
            return;
        }
        setCurrentScreen((curScreen) => curScreen + 1);
    };

    const handleStepChange = (curScreen) => {
        setCurrentScreen((curScreen));
    };
    
    // 어디 결과 페이지로 갈지 분기
    const goToResult = (testResult) => {
        let score = Object.values(testResult).reduce((sum, val) => sum + val);

        // 라우팅
        let route = '';
        if(score <= 14) {
          route = TEST_RESULTS_URL['1'];
        } else if(score <= 18) {
          route = TEST_RESULTS_URL['2'];
        } else if(score <= 20) {
          route = TEST_RESULTS_URL['3'];
        } else if(score <= 22) {
          route = TEST_RESULTS_URL['4'];
        } else if(score <= 26) {
          route = TEST_RESULTS_URL['5'];
        } else if(score <= 28) {
          route = TEST_RESULTS_URL['6'];
        } else if(score <= 31) {
          route = TEST_RESULTS_URL['7'];
        } else if(score <= 35) {
          route = TEST_RESULTS_URL['8'];
        } else if(score <= 38) {
          route = TEST_RESULTS_URL['9'];
        } else if(score <= 40) {
          route = TEST_RESULTS_URL['10'];
        }

        history.push({
            pathname: `/result/${route}`,
            state: {
                isAfterTest: true
            }
        });
    };

    useEffect(() => {
        setIsLoading(false);
    }, []);

    return isLoading ? null : (
        <div id="test-main-container">
          <hr id="header-bar"/>
          <div id="progress-text-container">{currentScreen + 1}/{TEST_DATA.length}</div>
          <SwipeableViews 
              enableMouseEvents={true} 
              index={currentScreen}
              onChangeIndex={handleStepChange}
              disabled={true}
              steps={TEST_DATA.length - 1}
              position="static"
              style={{width: '100%', maxWidth: 400, display: 'inline-block'}}>
              {TEST_DATA.map((test, index) => {
                  return (
                      <div key={index} style={{ display: index === currentScreen ? 'block' : 'none' }}>
                          <div className="description">{test.description}</div>
                          <div className="button-container">
                              {test.answer.map((ans, idx) => (
                                  <button 
                                      key={`${index}_${idx}`} 
                                      className="test-button" 
                                      {...mouseOverEvent}
                                      style={{WebkitTapHighlightColor: 'transparent'}}
                                      onClick={() => handleNext(index + 1, ans.value)} >{ans.text}</button>
                              ))}
                          </div>
                      </div>
                  )
              })}
          </SwipeableViews>
        </div>
    );
}

export default Test;

