import React from 'react';
import './Result.css';
import { TEST_RESULTS_DATA, TEST_URL } from '../test_data';
import { Link } from 'react-router-dom';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import imgApply from '../assets/apply.png';
import pearl from '../assets/result/pearl.jpg';
import opal from '../assets/result/opal.jpg';
import topaz from '../assets/result/topaz.jpg';
import aquamarine from '../assets/result/aquamarine.jpg';
import emerald from '../assets/result/emerald.jpg';
import amethyst from '../assets/result/amethyst.jpg';
import turquoise from '../assets/result/turquoise.jpg';
import ruby from '../assets/result/ruby.jpg';
import sapphire from '../assets/result/sapphire.jpg';
import diamond from '../assets/result/diamond.jpg';

const KAKAO_API_KEY = '755a3989b44a3843e2efb6fc16befd5f';

const Result = (state) => {
    const curruntUrl = window.location.href;
    const curruntId = state?.match?.params?.id;
    const result = TEST_RESULTS_DATA[curruntId];
    let mainImg = '';

    if(!result) {
        state.history.push({pathname: `/`});
    }

    switch (curruntId) {
        case 'pearl':
            mainImg = pearl;
            break;
        case 'opal':
            mainImg = opal;
            break;
        case 'topaz':
            mainImg = topaz;
            break;
        case 'aquamarine':
            mainImg = aquamarine;
            break;
        case 'emerald':
            mainImg = emerald;
            break;
        case 'amethyst':
            mainImg = amethyst;
            break;
        case 'turquoise':
            mainImg = turquoise;
            break;
        case 'ruby':
            mainImg = ruby;
            break;
        case 'sapphire':
            mainImg = sapphire;
            break;
        case 'diamond':
            mainImg = diamond;
            break;
    }
    
    const mouseOverEvent = {
        onMouseEnter: (e) => e.target.style.transform = 'scale(1.1)',
        onPointerEnter: (e) => e.target.style.transform = 'scale(1.1)',
        onMouseLeave: (e) => e.target.style.transform = 'scale(1.0)',
        onPointerLeave: (e) => e.target.style.transform = 'scale(1.0)',
    };

    const onCopy = () => {
        alert('URL이 복사되었습니다.');
    }

    const onClickKaKao = () => {
        if(!window.Kakao.isInitialized()) {
            window.Kakao.init(KAKAO_API_KEY);
        }

        window.Kakao.Link.sendDefault({
            objectType: 'feed',
            content: {
                title: '나는 관종일까 아닐까!?',
                description: '2021 관종레벨로 추천하는 당신의 직업 - 어게인 스쿨',
                imageUrl: `${TEST_URL}/logo.jpg`,
                link: {
                    mobileWebUrl: curruntUrl,
                    webUrl: curruntUrl
                }
            }
        });
    };

    return (
        <div id="result-main-container">
            <hr id="header-bar"/>
            <div id="content-container">
                <div id="main-text">{result['name']}</div>
                <div id="logo-container">
                    <img id="logo" src={mainImg} alt="logo" />
                </div>
                <div id="sub-text">{result['descript'].join('\n\n')}</div>
            </div>

            <div id="share-container">
                <div id="share-text">친구에게 공유하기</div>
                <div id="div-share-button1">
                    <CopyToClipboard onCopy={onCopy} text={window.location.href} {...mouseOverEvent}>
                        <button id="btn-share-url" className="url-copy-btn share-button" />
                    </CopyToClipboard>
                    <button id="btn-share-kakao" className="share-button" onClick={onClickKaKao} {...mouseOverEvent}/>
                    <a href={`http://www.facebook.com/sharer/sharer.php?u=${curruntUrl}`} target="_blank" rel="noopener noreferrer" {...mouseOverEvent}>
                        <button id="btn-share-facebook" className="share-button"/>
                    </a>
                    <a href={`https://twitter.com/intent/tweet?url=${curruntUrl}`} target="_blank" rel="noopener noreferrer" {...mouseOverEvent}>
                        <button id="btn-share-twitter" className="share-button"/>
                    </a>
                </div>

                <div id="div-share-button2">
                    <Link to={{pathname: '/'}}>
                        <button id="btn-restart-test" {...mouseOverEvent} />
                    </Link>
                    <a href="http://www.againschool.imweb.me" target="_blank" rel="noopener noreferrer" {...mouseOverEvent}>
                        <button id="btn-share-againschool" className="share-button"/>
                    </a>
                    <a href="https://apps.apple.com/kr/app/againschool/id1546449744" target="_blank" rel="noopener noreferrer" {...mouseOverEvent}>
                        <button id="btn-share-instargram" className="share-button"/>
                    </a>
                </div>
            </div>

            <div id="apply-container">
                <a href="https://form.office.naver.com/form/responseViewMobile.cmd?formkey=ZDE5MDI4YWItNGNiNi00MmYxLThkZWQtMDg4ZjRmMDU2OTFk" target="_blank" rel="noopener noreferrer" >
                    <img id="img-apply" src={imgApply} alt="" />
                </a>
            </div>

            <hr id="footer-bar"/>
        </div>
    )
};

export default Result;